
.footer {
  background-color: $color-white-smoke;
}

.footer__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.footer__social {
  display: grid;
  grid-template-columns: 1fr 4fr;
  column-gap: 50px;
  align-self: center;
  width: 100%;
}

.footer__logo {
  align-self: center;
}

.footer__list {
  grid-area: 1 / 2;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.footer__list-item {
  a {
    font-size: 18px;
    line-height: 150%;
    font-weight: 700;

    @include vp-1023 {
      font-size: 16px;
    }

    @include vp-767 {
      font-size: 14px;
    }

    &:hover,
    &:focus-within {
      color: $color-hollywood-cerise;
    }
  }
}

.footer__icon {
  display: block;
  width: 32px;
  height: 32px;
}
